@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.breaker {
  display: none;
}
@media (min-width: 768px) {
  .breaker--on-on {
    display: inline;
  }
}
@media (min-width: 1200px) {
  .breaker--on-on {
    display: inline;
  }
}
@media (min-width: 768px) {
  .breaker--on-off {
    display: inline;
  }
}
@media (min-width: 1200px) {
  .breaker--on-off {
    display: none;
  }
}
@media (min-width: 768px) {
  .breaker--off-on {
    display: none;
  }
}
@media (min-width: 1200px) {
  .breaker--off-on {
    display: inline;
  }
}

.button {
  display: inline-block;
  padding: 8px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  font-family: inherit;
  font-size: 21px;
  line-height: 42px;
  font-weight: 700;
  background-color: #81b3d2;
  border: 0;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}

.page {
  height: 100%;
}

.page__body {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  color: #333333;
  background-color: #f2f2f2;
  margin: 0;
  height: 100%;
  min-width: 320px;
}

.page__container {
  display: flex;
  margin: 0 auto;
  min-height: 100%;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  width: 320px;
}
@media (min-width: 768px) {
  .page__container {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .page__container {
    width: 1200px;
  }
}

.page__modal-container-mistake,
.page__modal-container-feedback {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.page__modal-container-mistake--close,
.page__modal-container-feedback--close {
  display: none;
}

.main-header {
  position: relative;
}

.main-header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
@media (min-width: 768px) {
  .main-header__logo {
    z-index: 3;
  }
}

.main-nav {
  display: flex;
  justify-content: flex-end;
  min-height: 56px;
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
  display: block;
}
.main-nav--nojs .main-nav__toggle-open {
  display: none;
}
.main-nav--nojs .main-nav__toggle-close {
  display: none;
}

.main-nav__toggle-open {
  display: block;
  background-color: transparent;
  margin: 9px 12px;
}
.main-nav__toggle-open:hover, .main-nav__toggle-open:active {
  background-color: transparent;
}
.main-nav__toggle-open .main-nav__toggle-icon-open:hover {
  opacity: 0.6;
}
.main-nav__toggle-open .main-nav__toggle-icon-open:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle-open {
    display: none;
  }
}

.main-nav__toggle-close {
  position: absolute;
  content: "";
  top: 17px;
  right: 19px;
  width: 23px;
  height: 23px;
  padding: 0;
  background-color: transparent;
}
.main-nav__toggle-close:hover, .main-nav__toggle-close:active {
  background-color: transparent;
}
.main-nav__toggle-close .main-nav__toggle-icon-close:hover {
  opacity: 0.6;
}
.main-nav__toggle-close .main-nav__toggle-icon-close:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle-close {
    display: none;
  }
}

.main-nav__toggle-icon-open {
  display: block;
  color: #81b3d2;
}

.main-nav__toggle-icon-close {
  display: block;
  color: #ffffff;
}

.main-nav__wrapper {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  z-index: 2;
}
.main-nav__wrapper--closed {
  display: none;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: static;
    display: block;
  }
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 153.6px);
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: repeat(5, 240px);
  }
}

.main-nav__item {
  background-color: #81b3d2;
  border-bottom: 1px solid rgba(1, 1, 1, 0.2);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.main-nav__item:first-child {
  border-top: none;
  box-shadow: none;
}
.main-nav__item:last-child {
  border-bottom: none;
}
.main-nav__item:last-child::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 320px;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__item {
    background-color: #ffffff;
    border: none;
    box-shadow: none;
  }
  .main-nav__item:last-child::after {
    display: none;
  }
  .main-nav__item:nth-child(3) {
    grid-column-start: 4;
  }
  .main-nav__item:nth-child(7) {
    grid-column-start: 4;
  }
}

.main-nav__link {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center;
  padding: 17px 8px 12px;
  word-break: break-all;
}
.main-nav__link:hover {
  background-color: #669ec0;
}
.main-nav__link:active {
  background-color: #5496bd;
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__link {
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    padding-top: 19px;
    padding-bottom: 16px;
  }
  .main-nav__link:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
  .main-nav__link:active {
    background-color: #ffffff;
    opacity: 0.3;
  }
  .main-nav__link--active {
    color: #766357;
  }
}

.main-index {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.main-index__header {
  padding-top: 90px;
