  background-repeat: no-repeat, no-repeat;
  background-image: url("../img/divider-mobile.svg"), url("../img/welcome-background-mobile.jpg");
  background-position: bottom, top;
}
@media (min-resolution: 2dppx) {
  .hero-index {
    background-image: url("../img/divider-mobile.svg"), url("../img/welcome-background-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
