    background-image: url("../img/divider-tablet.svg"), url("../img/welcome-background-tablet.jpg");
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero-index {
    background-image: url("../img/divider-tablet.svg"), url("../img/welcome-background-tablet@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .hero-index {
    background-image: url("../img/divider-desktop.svg"), url("../img/welcome-background-desktop.jpg");
  }
}
