  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: repeat(4, auto);
  }
}

.advantages__item {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
.advantages__item:first-child {
  padding-top: 36px;
}
.advantages__item:first-child::before {
  position: absolute;
  content: url("../img/divider-mobile-advantages.svg");
  left: 0;
  top: -1px;
}
.advantages__item--underline:not(:last-child)::after {
  position: absolute;
  content: "";
  left: 18px;
  bottom: 1px;
  width: 284px;
  height: 1px;
  background-color: #d6d6d6;
  z-index: 3;
}
.advantages__item--first {
  grid-column: 1/4;
  grid-row: 1/3;
}
.advantages__item--second {
  grid-column: 1/4;
  grid-row: 3/4;
}
.advantages__item--third {
  grid-column: 1/2;
  grid-row: 4/5;
}
.advantages__item--fourth {
  grid-column: 2/3;
  grid-row: 4/5;
}
.advantages__item--fifth {
  grid-column: 3/4;
  grid-row: 4/5;
}
@media (min-width: 768px) {
  .advantages__item--underline::after {
    display: none;
  }
}
@media (min-width: 768px) {
  .advantages__item {
    flex-direction: row;
  }
  .advantages__item:first-child {
    padding-top: 0;
  }
  .advantages__item:first-child::before {
    display: none;
  }
}

.advantages__number {
