.field-text--phone, .field-text--email {
  position: relative;
}
.field-text--phone::before, .field-text--email::before {
  position: absolute;
  content: "";
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  right: 0;
  top: 21px;
  z-index: 1;
}
.field-text--phone::after, .field-text--email::after {
  position: absolute;
  content: "";
  z-index: 1;
}
.field-text--phone::after {
  top: 41px;
  right: 20px;
  width: 18px;
  height: 18px;
  background-image: url("../img/phone.svg");
}
.field-text--email::after {
  top: 44px;
  right: 21px;
  width: 16px;
  height: 12px;
  background-image: url("../img/mail.svg");
}

.field-text__label {
  text-transform: uppercase;
  padding-left: 16px;
}

.field-text__input {
  padding: 1px 0 0 12px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  background-color: #f2f2f2;
  font-family: inherit;
  border: 4px solid transparent;
}
.field-text__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.field-text__input:hover {
  background-color: #e5e5e5;
}
.field-text__input:active, .field-text__input:focus-visible {
  background-color: #ffffff;
  border: 4px solid #e5e5e5;
  outline: none;
}

.control__label {
  position: relative;
  display: block;
  padding-left: 62px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}

.control__mark {
  position: absolute;
  top: -11px;
  left: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  cursor: pointer;
}
.control__mark--radio {
  border-radius: 50%;
}
.control__mark--checkbox {
  border-radius: 4px;
}
.control__mark:hover {
  background-color: #e5e5e5;
}
.control__mark:active {
  background-color: #d5d5d5;
  outline: none;
}

.control__input:checked + .control__mark--radio::before {
  position: absolute;
  content: " ";
  width: 16px;
  height: 16px;
  top: 15px;
  left: 15px;
  border-radius: 50%;
  background-color: #333333;
  background-image: none;
}

.control__input:checked + .control__mark--radio:active::before {
  background-color: rgba(51, 51, 51, 0.3);
}

.control__input:focus + .control__mark--radio {
  background-color: #ffffff;
  outline: 4px solid #e5e5e5;
  outline-offset: -4px;
}

.control__input:checked + .control__mark--checkbox::before {
  position: absolute;
  content: " ";
  width: 22px;
  height: 16px;
  top: 15px;
  left: 12px;
  background-image: url("../img/tick.svg");
}

.control__input:checked + .control__mark--checkbox:active::before {
  opacity: 0.3;
}

.control__input:focus + .control__mark--checkbox {
  background-color: #ffffff;
  outline: 4px solid #e5e5e5;
  outline-offset: -4px;
}

